import * as firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/analytics'
import 'firebase/firestore'

const firebaseConfig = {
  apiKey: process.env.GATSBY_API_KEY,
  authDomain: `${process.env.GATSBY_PROJECT_ID}.firebaseapp.com`,
  databaseURL: `${process.env.GATSBY_PROJECT_ID}.firebaseio.com`,
  projectId: process.env.GATSBY_PROJECT_ID,
  storageBucket: `${process.env.GATSBY_PROJECT_ID}.appspot.com`,
  messagingSenderId: process.env.GATSBY_MESSAGING_SENDER_ID,
  appId: process.env.GATSBY_APP_ID,
  measurementId: `G-${process.env.GATSBY_MEASURAMENT_ID}`
}

const lazy = fn => {
  let isLoaded = false
  let result
  return () => {
    if (!isLoaded) {
      isLoaded = true
      result = fn()
    }
    return result
  }
}

export const app = lazy(() => firebase.initializeApp(firebaseConfig))
export const auth = lazy(() => app().auth())
export const db = lazy(() => app().firestore())
